@import '../../assets/variables';

.confirmation {
  padding: 20px 20px 100px;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #eeeff3;
    margin-bottom: 20px;

    &__headline {
      font-size: 20px;
      color: #292727;
      font-weight: 400;
    }

    &__link-back {
      text-decoration: none;
      font-size: 12px;
      padding: 10px 20px 10px 17px;
      color: #e2b62e;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 95px;
      background: #ffffff;
      font-weight: 600;
      border-radius: 4px;
      border: 2px solid #ffcf3c;

      &:before {
        content: '';
        display: block;
        top: 21px;
        left: 30px;
        width: 10px;
        height: 10px;
        border: 2px solid #ffcf3c;
        border-top: none;
        border-right: none;
        transform: rotate(45deg);
      }
    }
  }

  &__order-info {
    position: relative;
    background-color: #fff;
    padding: 20px;
    border-radius: 4px;

    &:last-child {
      box-shadow: 0 18px 19px -15px rgba(0, 0, 0, 0.14)
    }

    &__table {

      &__headline {
        font-size: 12px;
        font-weight: 600;
        margin-bottom: 10px;
        color: #9E9E9E;
      }

      &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;

        &__item {

          strong {
            font-weight: 700;
          }

          &__button {
            cursor: pointer;
            
           img {
             width: 20px;
             height: 20px;
           }
          }
        }
      }

      &__order-table {

        &-row {
          display: flex;
          align-items: center;
          font-size: 14px;
          position: relative;
          line-height: 28px;

          &__cell {

            &:first-child {
              flex-grow: 1;
            }

            &:last-child {
              display: flex;
              align-items: center;
              flex-shrink: 0;
            }
          }

          button {
            border: none;
            padding: 0;
            background-color: transparent;
            margin-left: 10px;
            cursor: pointer;

            img {
              width: 20px;
              height: 20px;
            }
          }
        }
      }
    }
  }

  &__order-info + &__order-info {
    margin-top: 2px;

    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 14px;
      height: 14px;
      background-color: #f0f1f5;
      transform: rotate(45deg);
      top: -8px;
      left: -7px;
    }

    &:after {
      right: -7px;
      left: auto;
    }
  }

  &__alarm {
    display: flex;
    align-items: center;
    margin: 30px 0;
    font-size: 14px;
    line-height: 1.6;
    font-weight: 600;
    color: #292929;
    text-align: center;
    border: 2px solid #ffcf3d;
    border-left: 0;
    border-right: 0;
    padding: 20px 0;

    &__icon {
      width: 40px;
      height: 40px;
      margin: 0 10px;
    }
  }

  &__form {
    background-color: #fff;
    padding: 20px 10px;
    border-radius: 4px;
    box-shadow: 0 18px 19px -15px rgba(0, 0, 0, 0.14);

    &__item {
      display: flex;
      align-items: center;
      padding: 10px;

      &__input {
        border-radius: 2px;
        width: 100%;
        padding: 5px 10px;
        font-size: 14px;
        font-weight: 600;
        border: none;
        background: #f0f1f5;
        line-height: 28px;
        border-bottom: 2px solid transparent;

        &:focus,
        &:active,
        &:hover {
          outline: none;
          box-shadow: none;
        }

        &:focus {
          border-bottom-color: #ffcf3c;
          border-radius: 2px 2px 0 0;
        }

        &::-webkit-input-placeholder {
          font-weight: 400;
          color: #333;
        }
        &::-moz-placeholder {
          font-weight: 400;
          color: #333;
        }/* Firefox 19+ */
        &:-moz-placeholder {
          font-weight: 400;
          color: #333;
        }/* Firefox 18- */
        &:-ms-input-placeholder {
          font-weight: 400;
          color: #333;
        }
      }
    }
  }
}
