@import '../../assets/variables';

.button {
  display: inline-flex;
  align-items: center;
  min-height: 30px;
  padding: 3px;
  border-radius: 4px;
  background: $orange;
  color: #272727;
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;

  span {
    background-color: #fff;
    padding: 2px 16px;
  }
}