@import '../../assets/variables';

.datetime-page {
  display: flex;
  flex-direction: column;

  &__header {
    position: relative;
    z-index: 2;

    &__body {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #eeeff3;
      padding: 20px;

      &__page-caption {
        font-size: 20px;
        color: #292727;
        font-weight: 400;
      }

      &__link-back {
        text-decoration: none;
        font-size: 14px;
        padding: 10px 26px 10px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 600;
        border-radius: 4px;
        background: $orange;
        color: #fff;

        &:before {
          content: '';
          display: block;
          top: 21px;
          left: 30px;
          width: 10px;
          height: 10px;
          border: 2px solid #fff;
          border-top: none;
          border-right: none;
          transform: rotate(45deg);
          margin-right: 10px;
        }
      }

    }

    &__sub-headline {
      font-size: 14px;
      margin: 20px 20px 10px;
    }
  }
}