@import '../../assets/variables';

.employee-list {
  display: flex;
  background: #fff;
  overflow: auto;
  margin: 0;
  padding: 0;

  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    right: 20px;
    width: 50px;
    background: linear-gradient(to right, #ffffff 0%, #f1f1f1ad 100%);
    height: 81px;
    border-radius: 0 4px 4px 0;
  }
  
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 70px;
    padding: 12px 4px;
    flex-shrink: 0;
    text-align: center;
    z-index: 2;

    &--button {
      width: auto;
      display: flex;
      align-items: center;
      flex-direction: column;
    }

    &__button {
      width: 46px;
      height: 46px;
      border: 3px solid #a6a6ab;
      border-radius: 50%;
      overflow: hidden;
      position: relative;
      margin-top: 13px;
      margin-bottom: 0;

      &:before,
      &:after {
        content: '';
        position: absolute;
        border: 3px solid #a6a6ab;
        border-radius: 50%;

        .employee-list__item--active & {
          border-color: $orange;
        }
      }

      &:before {
        top: 5px;
        left: 9px;
        width: 22px;
        height: 22px;
      }

      &:after {
        top: 28px;
        left: 1px;
        width: 40px;
        height: 40px;
      }

      .employee-list__item--active & {
        border-color: $orange;
      }
    }

    &__check-mark {
      position: absolute;
      top: 8px;
      right: 4px;
      width: 20px;
      height: 20px;
      background: $orange;
      border-radius: 50%;
      display: none;

      .employee-list__item--active & {
        display: block;
      }

      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 5px;
        width: 10px;
        height: 6px;
        border: 2px solid #fff;
        border-top: none;
        border-right: none;
        transform: rotate(-45deg);
      }
    }

    &__img {
      width: 46px;
      height: 46px;
      margin: auto;
      border-radius: 50%;
      border: 3px solid #bbb;
      
      .employee-list__item--active & {
        border-color: $orange;
      }
    }

    &__caption,
    &__caption--default {
      font-size: 12px;
      white-space: nowrap;
      margin-top: 4px;

      &--default {
        margin-top: 3px;
      }
    }
  }
}
