@import '../../assets/variables';

.services-section {
  flex-grow: 1;
  overflow: auto;
  display: flex;
  flex-direction: column;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    height: 70px;
    background: linear-gradient(to top, rgba(162, 160, 160, 0.7) 0%, transparent 100%);
    pointer-events: none;
  }

  &__headline {
    font-size: 14px;
    margin: 20px 20px 14px 20px;
    font-weight: normal;
  }

  &__list {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    padding: 0 14px 65px;
    flex-grow: 1;
    overflow: auto;
  }
}
