@import '../../assets/variables';

.employee-section {
  background-color: #fff;
  margin: 20px 20px 0;
  padding: 20px 20px 10px;
  position: relative;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  
  &__headline {
    font-size: 16px;
    margin: 0 0 3px;
  }

  &__sub-headline {
    font-size: 14px;
    margin: 5px 0 24px;
    font-weight: normal;

    span {
      display: inline-block;
      font-size: 12px;
      white-space: nowrap;
      margin-right: 5px;

      + span {
        font-weight: 600;
        margin-right: 0;
      }
    }

    &-default {
      margin-bottom: 8px;
    }
  }
  
  &__copy-default-info {
    font-size: 12px;
    color: #9e9e9e;

    span {
      display: inline-block;
      white-space: nowrap;
      line-height: 1.4;
    }
  }
}
